  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.yaoyao {
  background: url("../assets/img/img_yaoyaole_bg.png") no-repeat;
  background-size: 100% 100%;
  background-color: #000;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  .nav {
    font-size: 28px;
    z-index: 999;
    padding: 30px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    div {
      
    div{
margin-left: 30px;
    }
      & > a {
        font-size: 22px;
        font-family: PingFangSC-Regular;
        font-weight: 400;
        text-align: center;
        color: rgba(255, 255, 255, 1);
        display: flex;
        flex-flow: column nowrap;
      }
    }

    div:last-child {
      display: flex;
      flex-flow: row nowrap;
    }
    img {
      max-width: 56px;
      margin-bottom: 16px;
    }
    .rang_list {
      // width: 633px;
      img {
        width: 100%;
        max-width: 600px;
      }
    }
  }
}
.content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.yaoyao_box {
  background: url("../assets/img/img_yaoyaole_jiqi.png") no-repeat;
  background-size: 100% 100%;
  width: 975px;
  min-height: 571px;
  margin: 0 auto;
  margin-bottom: 20px;
  position: relative;
  display: flex;
}
.list_item {
  li {
    position: absolute;
    width: 120px;
    height: 120px;
    background-image: url("../assets/img/img_yaoyaole_guntu.png");
    background-repeat: repeat;
    background-origin: center;
    position: absolute;
    top: 45%;
    transition: all 3s 0.5s ease-out;
    img {
      width: 110%;
      margin-left: -6px;
    }
    &:nth-child(1) {
      left: 23.5%;
    }
    &:nth-child(2) {
      left: 42%;
      transition-delay: 0.6s;
    }
    &:nth-child(3) {
      left: 61%;
      transition-delay: 0.7s;
    }
  }
}
.clones_box {
  align-self: flex-end;
  width: 100%;
  // height: 30px;
  margin-bottom:-6px;
  display: flex;
  align-content: center;
  justify-content: space-between;
  padding: 40px 140px;
  a {
    &:active {
      transform: scale3d(0.9, 0.9, 0.9);
      transform-origin: 50% 100%;
    }
  }
  &>div:first-child{
   padding-top: 8px;
  }
  div {
    margin-bottom: -5px;
    display: flex;
    align-content: center;
    justify-content: center;
    // align-items: center;
  }
  img {
    width: 100%;
  }

  input {
    color: #fff;
    text-align: center;
    width: 324px;
    height: 50px;
    transform: rotateX(-3deg);
    background: rgba(110, 11, 19, 1);
    border: 2px solid rgba(189, 32, 37, 1);
    box-shadow: 0px 2px 4px 0px rgba(252, 111, 111, 0.68),
      0px 6px 7px 0px rgba(172, 36, 36, 0.48),
      0px 2px 18px 0px rgba(50, 12, 12, 0.5),
      0px 0px 18px 0px rgba(50, 12, 12, 0.5);
    border-radius: 10px;
    outline: none;
    font-size: 30px;
    &:disabled{  

    color:#fff;
    opacity: 1;

    -webkit-text-fill-color: #fff;
}
  }
}
.btn {
  width: 56px;
  height: 38px;
  margin: 0 10px;
}
.player {
  width: 142px;
  height: 67px;
  &:active {
    transform: scale3d(0.9, 0.9, 0.9);
    transform-origin: 50% 100%;
  }
}
.rangkTitle {
  width: 311px;
}
.rangkbox {
  background: url("../assets/img/img_matchRecords_formBg.png") no-repeat;
  width: 100%;
  box-sizing: border-box;
  margin-top: -10px;
  background-size: 100% 100%;
  height: 380px;
  padding: 40px;
  ul {
    li {
      color: #fff;
      line-height: 55px;
      position: relative;
      display: flex;
      justify-content: space-around;
      span,
      h2 {
        font-size: 24px;
        font-family:PingFangSC-Regular;
        width: 40%;
        text-align: center;
      }
      h2 {
        font-size: 30px;
        font-family: DFPYuanW9;
        font-weight: bold;
        color: rgba(255, 255, 255, 1);
        text-stroke: 0.05em #863d2f;
        -webkit-text-stroke: 0.05em #863d2f;
        // line-height: 20px;
      }
      // img{
      //   // width: 28px;
      //   margin-left:10px;
      // }
      span:last-child {
        display: flex;
        // flex: 1;
        justify-content: space-around;
        align-items: center;
        img {
          max-height: 30px;
        }
        // height: 28px;
      }
       &:not(:last-child):before  {
        content: "";
        min-width: 95%;
        height: 5px;
        bottom: 0;
        // left: 0;
        // right: 0;
        position: absolute;
        // background:rgba(0,0,0,1);
        // border-radius: 10%;
        // box-shadow:0px 1px 1px 0px rgba(255,255,255,0.3), 0px 2px 0px 0px rgba(0, 0, 0, 0.77);
        background: url("../assets/img/img_matchRecords_divider.png") no-repeat;
        background-size: 100% 100%;
      }
    }
  }
}
.btn-bs {
  width: 70px;
  height: 123px;
  background: url("../assets/img/img_yaoyaolejiqi_zhuanlun2.png") no-repeat;
  background-size: 100% 100%;
  transition: all 3s 0.5s ease-out;

  transform-origin: 0 100%;
  position: absolute;
  top: 27%;
  right: 3%;
}
.btn-bs.active {
  animation: btnBs ease-in 0.3s 2 alternate forwards;
}
@keyframes btnBs {
  0% {
    transform: scaleY(1) translateY(0px) rotateZ(0deg);
  }
  100% {
    transform: scaleY(-1) translateY(-23px) rotateZ(-11deg);
  }
}
.balance_box {
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  height: 80px;
  top: 10%;
  display: flex;
  justify-content: center;
  justify-items: center;
  img {
    width: 131px;
    height: 38px;
    vertical-align: middle;
    align-self: center;
  }
  span {
    font-size: 26px;
    font-family: STYuanti-SC-Regular;
    font-weight: bold;
    line-height: 80px;
    color: rgba(255, 255, 255, 1);
    -webkit-text-stroke: .5px #c76d07;
    text-stroke: .5px #c76d07;
     -webkit-text-fill-color: #fff;
    img{
      vertical-align: sub;
      width: 32px;
      height: 28px;
      margin-left:10px 
    }
  }
}
